.homeSection {
    height: 100vh;
    .content {
        margin-left: 68px;
        position: relative;
    }

    .text1 {
        font-size: 72px;
        color: white;
        padding-top: 450px;
    }

    .text2 {
        width: 436px;
        color: white;
        margin-top: 72px;
        line-height: 40px;
        font-size: 24px;
    }

    .exploreButton {
        width: 161px;
        height: 72px;
        margin-top: 48px;
        text-transform: none;
        color: white;
        font-size: 18px;
        font-family: 'Klavika Md';
        background: linear-gradient(135deg, #2d61e5 0%, #8a62f6 53.09%, #e3477e 100%);
        box-shadow: 0px 0px 1px rgba(45, 97, 229, 0.24), 0px 2px 4px -1px rgba(10, 70, 82, 0.12),
            0px 16px 24px rgba(45, 97, 229, 0.24), 0px 8px 8px -4px rgba(45, 97, 229, 0.12);
        border-radius: 12px;
    }

    .landingVideo {
        position: absolute;
        width: 100%;
        height: 100vh;
        object-fit: fill;
        top: 0px;
    }
}
